@import "../Styles/Colors.scss";

.ContentContainer {
  height: calc(100vh - 60px - 10rem);
  display: flex;
  flex-direction: initial;
  align-items: center;
  width: auto;
  overflow: auto;
  overflowx: scroll;
  h1 {
    font-weight: lighter;
    font-size: 2rem;
  }
}
.DescriptionPlaceholder {
  height: 10vh;
}

.CollectionPageDescription {
  width: 20vw;
  min-width: 20vw;
  height: 100%;
  h1 {
    margin: 2rem 1rem;
  }

  p {
    margin: 2rem 1rem;
  }
}

.PhotoContainer {
  padding: 1rem;
  img {
    max-width: 60vw;
    max-height: 60vh;
  }
}
